.mainInfoObj {
  width: 90%;
  height: 70vh;
  margin: 1.5% 0 0 5%;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 30px;
  box-shadow: 0px 0px 15px #a5a4a4;
}
.mainInfoObj__text {
  position: absolute;
  top: 22%;
  left: 10%;
  text-align: center;
}
.mainInfoObj__text--h {
  font-size: 4rem;
}
.mainInfoObj__text--p {
  font-size: 2.5rem;
}
.mainInfoObj__text--pTriviaKing {
  font-weight: bold;
  font-size: 3rem;
}
.mainInfoObj__img {
  width: 40%;
  position: absolute;
  top: 17%;
  left: 52%;
}

.btnStart {
  position: relative;
  font-size: 2rem;
  position: absolute;
  top: 72%;
  left: 23.5%;
  background-color: rgba(46, 243, 46, 0.582);
  color: black;
  padding: 0.5%;
  border-radius: 20px;
  text-decoration: none;
}
.btnStart:hover {
  background-color: rgba(0, 248, 0, 0.767);
}